<template>

 <div class="box"> 
<h3>Personel Güncelle</h3>
      <form @submit.prevent="onUpdateForm">
 
          <input
            type="text"
            class="form-control"
            placeholder="Personel İsmi"
            v-model="personel.personel_isim"
            required
          />
          

        <div class="form-check-inline">
          <input type="radio" name="personel_durum" id="yurtta" value="Yurtta" v-model="personel.personel_durum" checked required> 
          <label for="inlineRadio1">Yurtta</label>
        </div>

        <div class="form-check-inline">
            <input type="radio" name="personel_durum" id="disarida" value="Dışarıda" v-model="personel.personel_durum" required>
         <label for="inlineRadio2">Dışarıda</label>
       </div>    


          <button class="btn btn-update">Güncelle</button>

      </form>
    </div>
</template>

<script>
import { db } from "../firebaseDb";
export default {
  data() {
    return {
      personel: {},
    };
  },
  created() {
    let dbRef = db.collection("personeller").doc(this.$route.params.id);
    dbRef
      .get()
      .then((doc) => {
        this.personel = doc.data();
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    onUpdateForm(event) {
      event.preventDefault();
      db.collection("personeller")
        .doc(this.$route.params.id)
        .update(this.personel)
        .then(() => {
          console.log("User successfully updated!");
          this.$router.push("/personellist");
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>